// ÂãïÊÖãÁîüÊàê metadata
export async function generateMetadata({
                                           params,
                                       }: {
    params: Promise<{ locale: string }>;
}): Promise<Metadata> {
    const { locale } = await params;

    // ÂèñÂæóË™ûÁ≥ªË≥áÊñô
    const languages = await I18nIntegrationService.getLanguages();
    const baseUrl = APP_CONFIG.API.BASE_URL;

    // ÁîüÊàê hreflang ÈÄ£Áµê
    const hrefLangLinks = await generateHrefLangLinks(languages, locale);

    // ËΩâÊèõÁÇ∫ Next.js metadata Ê†ºÂºè
    const alternates: Record<string, string> = {};
    hrefLangLinks.forEach(({ hrefLang, href }) => {
        alternates[hrefLang] = href;
    });

    // ÁîüÊàêÁµêÊßãÂåñË≥áÊñô
    const structuredData = generateMultilingualStructuredData(
        languages,
        locale,
        `${baseUrl}/${locale}`
    );

    return {
        title: {
            default: 'ÊàëÁöÑÁ∂≤Á´ô',
            template: '%s | ÊàëÁöÑÁ∂≤Á´ô',
        },
        description: 'ÈÄôÊòØÊàëÁ∂≤Á´ôÁöÑÈ†êË®≠ÊèèËø∞„ÄÇ',
        keywords: ['Next.js', 'React', 'ÈñãÁôº'],

        // ÊîπÈÄ≤ÁöÑ Open Graph ÈÖçÁΩÆ
        openGraph: {
            type: 'website',
            locale: locale.replace('-', '_'),
            url: `${baseUrl}/${locale}`,
            siteName: 'ÊàëÁöÑÁ∂≤Á´ô',
            title: 'ÊàëÁöÑÁ∂≤Á´ô',
            description: 'ÈÄôÊòØÊàëÁ∂≤Á´ôÁöÑÈ†êË®≠ÊèèËø∞„ÄÇ',
            images: [
                {
                    url: `${baseUrl}/og-image.jpg`,
                    width: 1200,
                    height: 630,
                    alt: 'ÊàëÁöÑÁ∂≤Á´ô',
                },
            ],
            alternateLocale: languages
                .filter(lang => lang.id !== locale)
                .map(lang => lang.id.replace('-', '_')),
        },

        // Ê∑ªÂä† Twitter Card
        twitter: {
            card: 'summary_large_image',
            title: 'ÊàëÁöÑÁ∂≤Á´ô',
            description: 'ÈÄôÊòØÊàëÁ∂≤Á´ôÁöÑÈ†êË®≠ÊèèËø∞„ÄÇ',
            images: [`${baseUrl}/og-image.jpg`],
        },

        // ÊîπÈÄ≤ÁöÑ alternates ÈÖçÁΩÆ
        alternates: {
            canonical: `${baseUrl}/${locale}`,
            languages: {
                ...alternates,
                'x-default': `${baseUrl}/${languages[0]?.id || 'en'}`,
            },
        },

        // Ê∑ªÂä†Êõ¥Â§ö SEO Áõ∏ÈóúÁöÑ metadata
        robots: {
            index: true,
            follow: true,
            googleBot: {
                index: true,
                follow: true,
                'max-video-preview': -1,
                'max-image-preview': 'large',
                'max-snippet': -1,
            },
        },

        // Ê∑ªÂä† verification Ê®ôÁ±§ÔºàÂ¶ÇÊûúÈúÄË¶ÅÔºâ
        verification: {
            google: 'your-google-verification-code',
        },

        other: {
            'language': locale,
            // Â∞áÁµêÊßãÂåñË≥áÊñôÊ∑ªÂä†Âà∞ other ‰∏≠
            'structured-data': JSON.stringify(structuredData),
        },
    };
}

export default async function LocaleLayout({
                                               children,
                                               params,
                                           }: {
    children: React.ReactNode;
    params: Promise<{ locale: string }>;
}) {
    const { locale } = await params;




    if (!hasLocale(routing.locales, locale)) {
        notFound();
    }

    setRequestLocale(locale);

    return (
        <html lang={locale}>
        <body className={`${geistSans.variable} ${geistMono.variable} antialiased`}>


        <NextIntlClientProvider>{children}</NextIntlClientProvider>
        </body>
        </html>
    );
}